@import url(https://fonts.googleapis.com/css2?family=Gemunu+Libre:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header {
    height: 5em;
    margin-bottom: 1.5em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: linear-gradient(0deg, rgb(226, 234, 252) 0%, #74c0f6 100%);
    border-bottom: 1px solid rgb(255, 255, 255);
}

body {
    background-color: #eeeffc;
}

input, select {
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 2.1em;
    text-align: center;
    color: #888;
    font-size: 1em;
}

.earthquake-list {
    background-color: rgba(247, 247, 250, 0.71);
    padding: 1em;
    border-radius: 8px;
    margin-bottom: 2em;
}

.list-item:hover {
    background-color: rgba(0,0,0,0.05);
}

.toolbar {
    margin-bottom: 1.5em;
}

.toolbar > * {
    width: 30%;
}

.toolbar-label {
    margin: 0.5em 0.6em 0.5em 0.5em;
}

h1, h2, h3, h4 {
    margin: 0;
}

header > h1, .insight-item h1 {
    font-family: 'Gemunu Libre', sans-serif;
    font-weight: normal;
    color: white;
}

.insight-item {
    width: 30%;
    transition: background-color 0.1s linear;
}

.insight-item h2 {
    font-weight: normal;
}

.insight-item:hover {
    background-color: rgba(0, 0, 0, 0.2);
}

.insight-item h1 {
    font-weight: normal;
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.1);
}

/* dark mode */
body {
    background-color: #202e3a;
    color: #a4e4f1;
}

.earthquake-list, .insight-item {
    background-color: rgba(0, 0, 0, 0.11);
    padding: 1em;
    border-radius: 8px;
    box-shadow: -1px 1px 10px 0 rgba(0,0,0,0.2);
}

header {
    background: linear-gradient(0deg, rgba(24, 91, 61, 0.15) 0%, #0e191c 100%);
    border-bottom: 1px solid rgb(145, 255, 152);
}

h1, h2, h3, h4 {
    color: #94d77e;
}

input, select {
    background-color: #383b49;
    border: 1px solid #537273;
}

.list-item {
    border-bottom: 1px solid rgba(20, 176, 49, 0.49);
}

.list-item:hover {
    border-bottom: 1px solid rgba(20, 176, 49, 1.0);
}

.toolbar {
    color: #fff7c7;
}
/* /dark mode */

